<template>
  <el-dialog :title="title" :visible.sync="visible" destroy-on-close class="preview_dialog" width="450px" :before-close="closeDialog">
    <div v-if="fileList.length>0">
      <div v-for="(item,i) in fileList" :key="i" class="itemfile">
        <el-image :src="file_icon" class="item-file-icon" />
        <div class="item-file-name" @click="previewFile(item)">
          {{ item.name }}
        </div>
        <i class="el-icon-download" @click="download(item)" />
      </div>
      <preview :dialog-state="dialogState" />
    </div>
    <div v-else class="empty-module">
      <el-image :src="empty" style="width: 60%;" />
      <div class="empty-tip"> 暂无数据</div>
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: "BatchPreview",
  components: {
    preview: () => import('@/components/BaseCom/preview/index'),
  },
  // model: {
  //   prop: 'visible',
  //   event: 'updateVisible', // 随便命名事件，对应下面$emit即可
  // },
  props: {
    title: {
      type: String,
      default: function() {
        return "文件预览";
      }
    },
    visible: {
      type: Boolean,
      default: function() {
        return false;
      }
    },
    fileList: { // 回显图片地址数组
      type: Array,
      default: function() {
        return [];
      }
    },
  },
  data() {
    return {
      dialogState: { show: false, url: '' },
      file_icon: require('./img/file_icon.png'),
      empty: require('./img/empty.png'),
    };
  },
  methods: {
    previewFile(file) {
      this.dialogState = { show: true, url: file.url }
    },
    closeDialog() {
       this.$emit('update:visible', false)
    },
    download({ url, name }) {
      const image = new Image()
      image.setAttribute('crossOrigin', 'anonymous')
      image.src = url
      image.onload = () => {
        const canvas = document.createElement('canvas')
        canvas.width = image.width
        canvas.height = image.height
        const ctx = canvas.getContext('2d')
        ctx.drawImage(image, 0, 0, image.width, image.height)
        canvas.toBlob((blob) => {
          const url = URL.createObjectURL(blob)
          const eleLink = document.createElement('a')
          eleLink.download = name
          eleLink.href = url
          eleLink.click()
          eleLink.remove()
          // 用完释放URL对象
          URL.revokeObjectURL(url)
        })
      }
      this.$message.success('下载成功');
    },
  }
};
</script>
<style lang="scss" scoped>
.preview_dialog {
  ::v-deep .el-dialog {
    border-radius: 5px;
    margin-top: 10vh !important;
    height: 50vh !important;
    .el-dialog__header {
      padding: 15px 20px;
      height: 54px;
      border-bottom: 1px solid #dedede;
      .el-dialog__title {
        font-size: 16px;
        color: #333;
      }
      .el-dialog__headerbtn {
        font-size: 25px;
        position: absolute;
        top: 15px;
        right: 20px;
      }
    }
    .el-dialog__body {
      padding: 0px 20px;
      font-size: 18px;
      height: calc(100% - 54px);
      overflow: auto;
    }
  }
}
.itemfile {
  display: flex;
  justify-content: space-between;
  height: 54px;
  align-items: center;
  border-bottom: 1px solid #f0f0f0;
}
.item-file-icon {
  width: 24px;
  height: 28px;
}
.item-file-name {
  width: calc(100% - 60px);
  color: #262626;
  font-size: 14px;
  cursor: pointer;
}
.empty-module {
  width: 100%;
  display: flex;
  height: 80%;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.el-icon-download {
  color: #0177d5;
  cursor: pointer;
}
.empty-tip {
  color: rgb(102, 102, 102);
  margin-top: 10px;
  font-size: 15px;
}
</style>
